@import "../bootstrap/bootstrap-common";

.icon {
  display: inline-block;
  vertical-align: middle;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: $transition-base;
}

.icon__expand { background-image: url(#{$rootPath}/images/blocks/icon/icon__expand.svg); }
.icon__expand_style_info { background-image: url(#{$rootPath}/images/blocks/icon/icon__expand_style_info.svg); }
.icon__shopping-bag { background-image: url(#{$rootPath}/images/blocks/icon/icon__shopping-bag.svg); }
.icon__heart { background-image: url(#{$rootPath}/images/blocks/icon/icon__heart.svg); }
.icon__clothing-size { background-image: url(#{$rootPath}/images/blocks/icon/icon__clothing-size.svg); }
.icon__close { background-image: url(#{$rootPath}/images/blocks/icon/icon__close.svg); }
.icon__collapse_style_info { background-image: url(#{$rootPath}/images/blocks/icon/icon__collapse_style_info.svg); }
.icon__collapse_style_primary { background-image: url(#{$rootPath}/images/blocks/icon/icon__collapse_style_primary.svg); }
.icon__add-to-shopping-bag { background-image: url(#{$rootPath}/images/blocks/icon/icon__add-to-shopping-bag.svg); }
.icon__choices { background-image: url(#{$rootPath}/images/blocks/icon/icon__choices.svg); }
.icon__rocket-ship { background-image: url(#{$rootPath}/images/blocks/icon/icon__rocket-ship.svg); }
.icon__wallet { background-image: url(#{$rootPath}/images/blocks/icon/icon__wallet.svg); }
.icon__arrow-down { background-image: url(#{$rootPath}/images/blocks/icon/icon__arrow-down.svg); }
.icon__trash { background-image: url(#{$rootPath}/images/blocks/icon/icon__trash.svg); }
.icon__menu { background-image: url(#{$rootPath}/images/blocks/icon/icon__menu.svg); }
.icon__cancel { background-image: url(#{$rootPath}/images/blocks/icon/icon__cancel.svg); }
.icon__search { background-image: url(#{$rootPath}/images/blocks/icon/icon__search.svg); }
.icon__paper-plane { background-image: url(#{$rootPath}/images/blocks/icon/icon__paper-plane.svg); }

.icon__vk { background-image: url(#{$rootPath}/images/blocks/icon/icon__vk.svg); }
.icon__instagram { background-image: url(#{$rootPath}/images/blocks/icon/icon__instagram.svg); }

.icon_style_dark {
  filter: brightness(0);
}
.icon_style_light {
  filter: brightness(0) invert(1);
}

[data-toggle="collapse"] {
  .icon__expand {
    transition: $transition-base;
  }
}
.collapsed {
  .icon__expand {
    transform: rotate(45deg);
  }
}